import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Typography, Button, ButtonGroup, Input, Select, MenuItem, Breadcrumbs } from '@mui/material';
import {
    Folder as FolderIcon,
    Check as CheckIcon,
    Cancel as CancelIcon,
    Add as AddIcon,
    ModeEdit as EditIcon,
    DeleteOutline as DeleteIcon,
    FileUpload as UploadIcon
} from '@mui/icons-material';

function ProductFolder({ folderArr, currentFolder, addFolder, setCurrentFolder, langPack }) {
    let [newFolder, setNewFolder] = useState(false);
    let [folderCommand, setFolderCommand] = useState(false);
    async function onFolderAdd() {
        if (!newFolder) return queryResult(false, langPack.folder.needFolderName);
        let result = await addFolder({ group_name: newFolder });
        if (result) {
            setCurrentFolder('');
            refreshFolders();
        }
        return;
    }
    function refreshFolders() {
        setNewFolder('');
        setFolderCommand(false);
        return;
    }

    return (
        <div style={{ marginBottom: '20px' }}>
            {!folderCommand ? (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                    <span>
                        {!folderArr || !folderArr.length ? (
                            <Typography variant="body2" sx={{ display: 'inline' }}>
                                {langPack.folder.folderNull}
                            </Typography>
                        ) : (
                            <Breadcrumbs aria-label="breadcrumb" sx={{ display: 'inline-block', marginRight: '5px' }}>
                                <FolderIcon />
                                <Select
                                    sx={{ height: '30px', minWidth: '100px' }}
                                    autoWidth={true}
                                    value={currentFolder}
                                    onChange={(e) => setCurrentFolder(e.target.value)}
                                >
                                    <MenuItem value={langPack.folder.currentFolder} disabled>
                                        {langPack.folder.currentFolder}
                                    </MenuItem>
                                    {folderArr.map((obj, index) => (
                                        <MenuItem key={index} value={obj.group_id}>
                                            {obj.group_name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Breadcrumbs>
                        )}
                        <ButtonGroup variant="">
                            <Button onClick={() => setFolderCommand('add')}>
                                <AddIcon />
                            </Button>
                            <Button onClick={() => setFolderCommand('edit')} disabled>
                                <EditIcon />
                            </Button>
                            <Button onClick={() => setFolderCommand('delete')} disabled>
                                <DeleteIcon />
                            </Button>
                            <Button onClick={() => setFolderCommand('upload')} disabled>
                                <UploadIcon />
                            </Button>
                        </ButtonGroup>
                    </span>
                </div>
            ) : (
                <>
                    {folderCommand === 'add' ? (
                        <>
                            <Input
                                sx={{ height: '30px', marginRight: '10px' }}
                                variant="outlined"
                                color="secondary"
                                type="text"
                                placeholder={langPack.folder.addNewFolder}
                                onChange={(e) => setNewFolder(e.target.value.trim())}
                            />
                            <Button startIcon={<CheckIcon />} onClick={onFolderAdd}>
                                {langPack.common.yes}
                            </Button>
                            <Button startIcon={<CancelIcon />} onClick={() => setFolderCommand(false)}>
                                {langPack.common.no}
                            </Button>
                        </>
                    ) : (
                        ''
                    )}
                </>
            )}
        </div>
    );
}

ProductFolder.propTypes = {
    localization: PropTypes.object,
    finalDescription: PropTypes.string,
    setFinalDescription: PropTypes.func,
    folderArr: PropTypes.array,
    currentFolder: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    addFolder: PropTypes.func,
    setCurrentFolder: PropTypes.func,
    langPack: PropTypes.object
};

export default ProductFolder;
